              <v-checkbox
                class="py-0"
                v-model="s2_pd_check.isCheckAds"
                label=""
                readonly
              >
              </v-checkbox>
            </v-row>
            <v-row class="mx-12" v-if="s2_pd_check.isCheckAds">
              <v-col cols="12" md="6" class="py-0">
                <v-radio-group
                  v-model="s2_pd_checkInfo.check_ads_status"
                  row
                  readonly
                >
                  <v-radio label="" value="0"></v-radio>
                  <v-radio label="" value="1"></v-radio>
                </v-radio-group>
              </v-col>
              <v-col cols="12" md="8" class="py-0">
                <v-text-field
                  label=""
                  outlined
                  rounded
                  v-model="s2_pd_checkInfo.check_ads_info"
                  background-color="grey lighten-4"
                  readonly
                ></v-text-field>
              </v-col>
            </v-row>
            <v-divider
              v-if="
                s2_pd_detail.pd_pdTypeIndex == 1 ||
                  s2_pd_detail.pd_pdTypeIndex == 2 ||
                  s2_pd_detail.pd_pdTypeIndex == 3 ||
                  s2_pd_detail.pd_pdTypeIndex == 4 ||
                  s2_pd_detail.pd_pdTypeIndex == 6 ||
                  s2_pd_detail.pd_pdTypeIndex == 7 ||
                  s2_pd_detail.pd_pdTypeIndex == 8 ||
                  s2_pd_detail.pd_pdTypeIndex == 9 ||
                  s2_pd_detail.pd_pdTypeIndex == 10
              "
            >
            </v-divider>

            <v-row
              v-if="
                s2_pd_detail.pd_pdTypeIndex == 2 ||
                  s2_pd_detail.pd_pdTypeIndex == 5 ||
                  s2_pd_detail.pd_pdTypeIndex == 7 ||
                  s2_pd_detail.pd_pdTypeIndex == 8 ||
                  s2_pd_detail.pd_pdTypeIndex == 9
              "
              class="mx-6"
            >
              <v-checkbox
                class="py-0"
                v-model="s2_pd_check.isCheckTestkit"
                label=" (Test-kit)"
                readonly
              >
              </v-checkbox>
            </v-row>
            <!-- Testkit list information -->
            <div v-if="s2_pd_check.isCheckTestkit" class="mx-6">
              <v-row justify="start" class="mt-4">
                <v-col cols="12" align="left" class="pa-0 mx-4">
                  <p class="sub-header_form">
                     (Test-kit)
                  </p>
                </v-col>
              </v-row>
              <v-row
                v-for="(content, i) in s2_testkit"
                :key="i"
                class="mx-6"
                align="center"
              >
                <v-col cols="12" class="py-0">
                  <v-row align="center">
                    <v-col cols="6" align="left" class="py-0">
                      <span class="text_itemNumber mb-8"
                        > {{ i + 1 }}
                      </span>
                      <v-img
                        :src="content.url"
                        aspect-ratio="1.0"
                        contain
                      ></v-img>
                    </v-col>

                    <v-col cols="5" align="left" class="py-0">
                      <v-text-field
                        class="my-0"
                        label=""
                        outlined
                        rounded
                        v-model="content.imgName"
                        background-color="grey lighten-4"
                        readonly
                      ></v-text-field>
                      <v-text-field
                        class="my-0"
                        label=""
                        outlined
                        rounded
                        v-model="content.name"
                        background-color="grey lighten-4"
                        readonly
                      ></v-text-field>
                      <v-text-field
                        class="my-0"
                        label=""
                        outlined
                        rounded
                        v-model="content.isPositiveName"
                        background-color="grey lighten-4"
                        readonly
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-col>
                <v-col cols="12" class="py-0">
                  <v-divider class="mb-6"></v-divider>
                </v-col>
              </v-row>
            </div>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <v-divider></v-divider>

    <!-- S3: Source of Product Detailed -->
    <v-row>
      <v-col cols="12" align="start">
        <p class="header_form">/</p>
        <DisplayImages :imagesArray="s3_src_photoShow" />
        <p class="sub-header_form">/</p>
        <v-row class="mx-4">
          <v-col cols="12" md="6" class="py-0">
            <v-text-field
              label=""
              v-model="s3_src_detail.src_srcTypeName"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>

        <!-- S3.1: About Source -->
        <p class="sub-header_form">/</p>
        <v-textarea
          class="mt-4 px-8"
          label="/"
          v-model="getAboutSource"
          filled
          background-color="grey lighten-4"
          readonly
          rounded
          outlined
          no-resize
          rows="3"
        >
        </v-textarea>
        <p class="sub-header_form">/</p>

        <!-- S3.2: Location Info -->
        <v-row class="mx-4">
          <v-col cols="12" class="py-0">
            <v-text-field
              label=""
              v-model="s3_src_detail.src_adrs"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="mx-4">
          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label=""
              v-model="s3_src_detail.src_prov"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="/"
              v-model="s3_src_detail.src_dist"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="/"
              v-model="s3_src_detail.src_subDist"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import DisplayImages from "./DisplayImages";
import moment from "moment";

export default {
  components: {
    DisplayImages
  },
  props: [
    "s1_user_info",
    "s1_pd_discover",
    "s2_pd_detail",
    "s2_pd_label",
    "s2_pd_licenseInfo",
    "s2_pd_manufacturer",
    "s2_pd_usage",
    "s2_pd_check",
    "s2_pd_checkInfo",
    "s2_drugSet",
    "s2_testkit",
    "s3_src_detail",
    // IMAGES
    "s2_pd_photoShow",
    "s3_src_photoShow"
  ],
  computed: {
    getAboutSource() {
      return (
        this.s3_src_detail.src_subSrcTypeName +
        " " +
        this.s3_src_detail.src_subInfoName +
        " " +
        this.computedDateFormattedMomentjs() +
        " " +
        this.s3_src_detail.src_timeOnAir +
        " " +
        this.s3_src_detail.src_pName +
        " " +
        this.s3_src_detail.src_pTel +
        " " +
        this.s3_src_detail.src_pContact +
        " " +
        this.s3_src_detail.src_pLicense +
        " " +
        this.s3_src_detail.src_pCar +
        " " +
        this.s3_src_detail.src_pCarColor
      );
    },
    DateFormatted_MFD() {
      let originYear = this.s2_pd_manufacturer.pd_mf_mfd.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s2_pd_manufacturer.pd_mf_mfd.replace(
        originYear,
        cvYearBE
      );
      return this.s2_pd_manufacturer.pd_mf_mfd
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    },
    DateFormatted_EXD() {
      let originYear = this.s2_pd_manufacturer.pd_mf_exd.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s2_pd_manufacturer.pd_mf_exd.replace(
        originYear,
        cvYearBE
      );
      return this.s2_pd_manufacturer.pd_mf_exd
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    }
  },
  methods: {
    computedDateFormattedMomentjs() {
      let originYear = this.s3_src_detail.src_dateOnAir.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      if (cvYearInt < 2000) {
        return "";
      }
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s3_src_detail.src_dateOnAir.replace(
        originYear,
        cvYearBE
      );
      return this.s3_src_detail.src_dateOnAir
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    }
  }
};
</script>

