.bodyContent {
  width: 100%;
  max-width: 1264px;
  margin: auto;
}
.header_form {
  font-size: 24px;
  font-weight: 500;
  color: #00b5f8;
}
.sub-header_form {
  font-size: 18px;
  font-weight: 600;
  color: #424242;
}
.text_itemNumber {
  color: #424242;
}
.rounded-custom {
  border-radius: 12px;
}
</style>
